<template>
    <div>
        <div 
            v-for="(item, idx) of items" :key="idx"
            class="item"
            @click="onItemClick(item)">
            <div class="txt-area">
                <h6 class="tit">{{item.title}}</h6>
                <p class="desc">{{item.desc}}</p>
                <div class="sub-info">
                    <span>{{item.writer}}</span>
                </div>
                <div class="util-box">
                    <span>{{item.views}}</span>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    name: 'TmpListBasic',
    props: {
        items: {
            type: Array,
        }
    },
    methods: {
        onItemClick: function(item) {
            // [NOTE]
            // Î∂ÄÎ™® v-model Ïóê ÎÑ£Ïñ¥Ïßà value Í∞íÏùÑ Ï†ÑÎã¨(v-modelÏóê Í∞íÏùÑ ÎÑ£Îäî ÏûëÏóÖÏùÄ v-modelÏùò inputÏù¥ ÏàòÌñâ, ÎÇòÎäî Í∑∏ÎÉ• v-modelÏóê Îì§Ïñ¥Í∞à valueÏóê Îì§Ïñ¥Í∞ÄÎäî Í∞íÏù¥ Î≠îÏßÄ Ï†ÑÎã¨Îßå Ìï¥Ï£ºÎ©¥ Îê®)
            // Îç∞Ïù¥ÌÑ∞ Ï†ÑÎã¨Ìï¥Ï£ºÎäî Í≤ÉÍ≥º, ÌÅ¥Î¶≠Ïóê Îî∞Î•∏ Ïù¥Î≤§Ìä∏Î•º Î∂ÑÎ¶¨ÏãúÌÇ§Í∏∞ ÏúÑÌï¥ Ïù¥Î≤§Ìä∏ ÎëêÍ∞ú ÏÇ¨Ïö©
            // Î∂ÄÎ™®ÏóêÏÑú click Ïù¥Î≤§Ìä∏Í∞Ä ÏûàÏùÑ ÏàòÎèÑ ÏûàÏúºÎãà, Ïª§Ïä§ÌÖÄ Ïù¥Î≤§Ìä∏Î™ÖÏùÑ clickÏúºÎ°ú ÏïàÌï¥ÎèÑ Îê†ÎìØ ÌïòÎã§.
            this.$emit('input', item);
            this.$emit('item-click');
        }
    }
}
</script>