<template>
    <div>
        <!-- [TODO]
            - ÏóÜÎäî ÌÉÄÏûÖÏù¥ Îì§Ïñ¥ÏôîÏùÑÎïå Ï≤òÎ¶¨
            - Îç∞Ïù¥ÌÑ∞Í∞Ä ÏóÜÏùÑ ÎïåÎäî? - Í≥†Î†§ ÏïàÌï¥Î¥Ñ
        -->
        <!-- Ïª¥Ìè¨ÎÑåÌä∏1: type1: basic: Í∏∞Î≥∏ÌÉÄÏûÖ -->
        <!-- Ïª¥Ìè¨ÎÑåÌä∏2: type2: gallery: Í∞§Îü¨Î¶¨ÌÉÄÏûÖ -->
        <!-- Ïª¥Ìè¨ÎÑåÌä∏3: type3: webzine: ÏõπÏßÑÌÉÄÏûÖ -->
        <tmp-list-basic
            v-model="theItem"
            v-if="listType === 'basic'"
            :items="items"
