            default: 'webzine',
            validator: function (type) {
                // Í∞íÏù¥ Ìï≠ÏÉÅ ÏïÑÎûò ÏÑ∏ Í∞úÏùò Î¨∏ÏûêÏó¥ Ï§ë ÌïòÎÇòÏó¨Ïïº Ìï©ÎãàÎã§. 
                console.log("prop:listtype", type);
                return ['basic', 'gallery', 'webzine'].indexOf(type) !== -1
            }
        },
        items: {
            type: Array,
        }
    },
    methods: {
        // [NOTE]
        // Î∂ÄÎ™® v-model Ïóê ÎÑ£Ïñ¥Ïßà value Í∞íÏùÑ Ï†ÑÎã¨(this.theItemÏóî Ïù¥ÎØ∏ ÏûêÏãùÏóêÏÑú Ï†ÑÎã¨Î∞õÏùÄ valueÍ∞Ä Îì§Ïñ¥Í∞ÄÏûàÏùå, Í∑∏ Í∞íÏùÑ Îòê ÏµúÏ¢Ö Î∂ÄÎ™®Ïùò v-modelÎ°ú toss ÌïòÍ∏∞ ÏúÑÌï¥ emitÏ†ÑÏÜ°, ÎÇòÎäî Í∑∏ÎÉ• v-modelÏóê Îì§Ïñ¥Í∞à valueÏóê Îì§Ïñ¥Í∞ÄÎäî Í∞íÏù¥ Î≠îÏßÄ Ï†ÑÎã¨Îßå Ìï¥Ï£ºÎ©¥ Îê®)
        tossTheItem: function() {
